<template>
  <div id="app">
    <img src="./assets/logo.png" style="height: 55px;">
    <h1 style="margin: 5px 0 40px;">התאמת מלונות ללקוחות</h1>
    <template>
      <div class="col-4" style="float: right; direction: rtl;">
        <b-form-input v-model="textFilter" placeholder="חפש לפי כיתוב"
                      class="" style="margin-bottom: 20px;">
        </b-form-input>
        <span style="display: flex;">
        <b-form-select class="" v-model="selected_country" :options="countries"
                       style="margin-bottom: 20px;"
                        @change="changeCountry"></b-form-select>
        <b-form-select class="" v-model="selected_town" :options="townsFiltered"
                       style="    margin-right: 18px; margin-bottom: 20px;"
                        @change="changeTown"></b-form-select>
      </span>
        <div style="display: flex;">
             כוכבים:
              <b-form-checkbox v-model="stars_1" style="display: table-cell;">
                <span style="display: flex">
                  <icon name='star'></icon>
                </span>
              </b-form-checkbox>
              <b-form-checkbox v-model="stars_2" style="display: table-cell;">
                <span style="display: flex">
                  <icon name='star'></icon><icon name='star'></icon>
                </span>
              </b-form-checkbox>
              <b-form-checkbox v-model="stars_3" style="display: table-cell;">
                <span style="display: flex">
                  <icon name='star'></icon><icon name='star'></icon><icon name='star'></icon>
                </span>
              </b-form-checkbox>
              <b-form-checkbox v-model="stars_4" style="display: table-cell;">
                <span style="display: flex">
                  <icon name='star'></icon><icon name='star'></icon><icon name='star'></icon><icon name='star'></icon>
                </span>
              </b-form-checkbox>
              <b-form-checkbox v-model="stars_5" style="display: table-cell;">
                <span style="display: flex">
                  <icon name='star'></icon><icon name='star'></icon><icon name='star'></icon><icon name='star'></icon><icon name='star'></icon>
                </span>
              </b-form-checkbox>
        </div>
        <div style="display: flex">
          <b-form-group label="למשפחות" style="margin: 8px 0px 15px 10px;">
            <b-form-checkbox-group v-model="families" :options="['לא','כן']" ></b-form-checkbox-group>
          </b-form-group>
          <b-form-group label="לצעירים" style="margin: 8px 0px 15px 10px;">
            <b-form-checkbox-group v-model="youngsters" :options="['לא','כן']" ></b-form-checkbox-group>
          </b-form-group>
          <b-form-group label="רמת מחירים ללילה" style="margin: 8px 0px 15px 10px;">
            <b-form-checkbox-group v-model="price_level" :options="[
              {value:'נמוכה', text: 'נמוכה - עד 500'},
              {value:'בינונית', text: 'בינונית - בין 500 ל1000'},
              {value:'גבוהה', text: 'גבוהה - מעל 1000'}]" ></b-form-checkbox-group>
          </b-form-group>
        </div>
      </div>
      <b-table responsive striped bordered hover style="direction: rtl"
               :filter="textFilter"
               :items="items_filtered" :fields="fields">
        <!--<template slot="index" slot-scope="data">-->
          <!--{{data.index + 1}}-->
        <!--</template>-->
        <template slot="מתאים_למשפחות" slot-scope="data">
          <b-form-checkbox id="checkbox2"
                           :disabled="true"
                           v-model="data.value === 'כן'">
          </b-form-checkbox>
        </template>
        <template slot="מתאים_לצעירים?" slot-scope="data">
          <b-form-checkbox id="checkbox1"
                           :disabled="true"
                           v-model="data.value === 'כן'">
          </b-form-checkbox>
        </template>
        <template slot="שם_המלון_באתר_טרוולאור" slot-scope="data">
          <a :href="`#${data.item.link.replace(/[^a-z]+/i,'-').toLowerCase()}`">
            {{data.value}}
          </a>
        </template>
        <template slot="uniqueness" slot-scope="data">
          <div>
            <b-btn v-b-toggle="'collapse'" class="m-1">לחץ להצגה</b-btn>
            <b-collapse :id="'collapse'">
              <b-card>
                {{data.value}}
              </b-card>
            </b-collapse>
          </div>
        </template>
        <template slot="tips" slot-scope="data">
          <div>
            <b-btn v-b-toggle="'collapse2'" class="m-1">לחץ להצגה</b-btn>
            <b-collapse id="collapse2">
              <b-card>
                {{data.value}}
              </b-card>
            </b-collapse>
          </div>
        </template>
        <template slot="הערות_נוספות" slot-scope="data">
          <div>
            <b-btn v-b-toggle="'collapse3'" class="m-1">לחץ להצגה</b-btn>
            <b-collapse id="collapse3">
              <b-card>
                {{data.value}}
              </b-card>
            </b-collapse>
          </div>
        </template>
      </b-table>
    </template>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      isBusy: true,
      fields:[
        // {key: 'index', label: ''},
        'ארץ', 'עיר', 'כוכבים',
        {
          key: 'שם_המלון_באתר_טרוולאור',
          label: 'שם המלון'
        },{
          key: 'סוג_האירוח_שניתן_להזמין',
          label: 'סוג האירוח'
        },{
          key: 'price_level',
          label: 'רמת מחירים'
        }, {
          key: 'מתאים_למשפחות',
          label: 'למשפחות'
        }, {
          key: 'מתאים_לצעירים?',
          label: 'לצעירים'
        }, {
          key: 'מתקני_המלון',
          label: 'מתקני המלון'
        }, {
          key: 'uniqueness',
          label: 'ייחוד'
        }, {
          key: 'מיקום_(_בשכונה_היהודית,_ליד_הדאם,_ליד_הרמבאללה...)',
          label: 'מיקום'
        }, {
          key: 'tips',
          label: 'טיפים'
        }, {
          key: 'הערות_נוספות',
          label: 'הערות'
        }
      ],
      items: [],
      countries: [],
      towns: [],
      townsFiltered: [],
      selected_country: null,
      selected_town: null,
      stars_1: true,
      stars_2: true,
      stars_3: true,
      stars_4: true,
      stars_5: true,
      families: ['לא','כן'],
      youngsters: ['לא','כן'],
      price_level: ['נמוכה','בינונית', 'גבוהה'],
      textFilter: '',
    }
  },
  created() {
    this.myProvider();
  },
  methods: {
    myProvider (ctx) {
      // Here we don't set isBusy prop, so busy state will be handled by table itself
      this.isBusy = true;
      let promise = this.axios.get('https://script.googleusercontent.com/macros/echo?user_content_key=Z_3WrbktGgXLd7jLnLv-U-boHjnuGPCQwxmyJTIO4s2xu1lPOjp_cM08iI6soNapU9VwVKXlflBgQsNF2a5YmtMQGivAYYhVOJmA1Yb3SEsKFZqtv3DaNYcMrmhZHmUMWojr9NvTBuBLhyHCd5hHa1ZsYSbt7G4nMhEEDL32U4DxjO7V7yvmJPXJTBuCiTGh3rUPjpYM_V0PJJG7TIaKp-zDUlTi5qbZ_epPHWRDswZ7v5c_gzmw7mqnNTKw_xGeiANDuxNWkp5g1Z4-Nm6BRw&lib=MbpKbbfePtAVndrs259dhPT7ROjQYJ8yx');

      return promise.then((data) => {
        console.log(data.data["תגובות לטופס 1"]);

        let countriesSet = new Set([{value: null, text:'בחר מדינה'}]);
        let townsSet = new Set([{value: null, text:'בחר עיר'}]);
        data.data["תגובות לטופס 1"].forEach(function (item) {
          item['link'] = item['קישור_לדף_המלון_בטרוולאור_-_יש_להכניס_קישור_כללי_וללא_תאריכים,_לדוגמה:_https://www.travelor.com/he/hotels/gr/hersonissos/star-beach-village'];
          item['uniqueness'] = item['ייחוד_(למה_להמליץ_על_מלון_זה_-_כשר,_מיקום_ליד_אטרקציות,_,_קרמחיר_וכדומה)'];
          item['tips'] = item['טיפים_(לבקש_חדר_גבוהה,_להזמין_רק_חדרים_מסוג_מסויים_,_לא_להזמין_מסוג_אחר_וכו\')'];
          item['families'] = item['מתאים_למשפחות'];
          item['youngsters'] = item['מתאים_לצעירים?'];
          if(item['רמת_מחירים_'] !== "") {
            item['price_level'] = item['רמת_מחירים_'] === "נמוכה - מתחת ל-500 ש\"ח ללילה" ? 'נמוכה' :
              item['רמת_מחירים_'] === "בינונית - בין 500 ש\"ח ל-1000 ש\"ח ללילה\n" ? 'בינונית' : 'גבוהה';
          }

          item['stars'] = Number(item['כוכבים'].split(" ")[0]);
          countriesSet.add(item['ארץ']);
          townsSet.add({value: item['עיר'], text: item['עיר'], country: item['ארץ']})
        });

        this.countries = Array.from(countriesSet.values());
        this.townsFiltered = this.towns = Array.from(townsSet.values());
        const items = data.data["תגובות לטופס 1"];
        this.isBusy = false;
        this.items = items;
      }).catch(error => {
        this.isBusy = false;
      })
    },
    changeCountry(selectedCountry) {
      if(selectedCountry){
        this.townsFiltered = this.towns.filter(value => value.country === selectedCountry);
      } else  {
        this.townsFiltered = this.towns;
      }
      this.selected_town = null;
    },
    changeTown(selectedTown) {
      if(selectedTown) {
        this.selected_country = this.towns.filter(town => town.value === selectedTown)[0].country;
      } else {
        changeCountry(this.selected_country)
      }
    }
  },
  computed: {
    items_filtered: function () {
      let self = this;
      return this.items.filter(value => this.selected_country == null || value['ארץ'] === this.selected_country)
        .filter(value => this.selected_town == null || value['עיר'] === this.selected_town)
        .filter(value => {
          return (self.stars_1 && value.stars === 1) ||
            (self.stars_2 && value.stars === 2) ||
            (self.stars_3 && value.stars === 3) ||
            (self.stars_4 && value.stars === 4) ||
            (self.stars_5 && value.stars === 5)
        })
        .filter(value => this.families.filter(
          familyValueSelected => value.families === familyValueSelected).length > 0)
        .filter(value => this.youngsters.filter(
          youngstersValueSelected => value.youngsters === youngstersValueSelected).length > 0)
        .filter(value => value['price_level'] !== null || this.price_level.filter(
          priceLevelSelected => value.price_level === priceLevelSelected).length > 0);

    }
  }
}
</script>

